.input {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 10px;
  padding-left: 10px;
  margin-left: 10px;
  width: 240px;
  border: 1px solid #c1c1c1;
  box-shadow: 3px 8px 14px rgba(136, 198, 253, 0.19);
  border-radius: 10px;
}

.input:focus {
  background-color: rgb(226, 191, 149);
}

.label {
  font-size: 20px;
}
